import './App.css';
import { useState, useEffect } from 'react';
import axios from 'axios';
function App() {
  const [newNote, setNewNote] = useState({title:'', text:''})
  const [editingNote, setEditingNote] = useState({title:'', text:'', state:false})
  const [notes, setNotes] = useState([])
    useEffect(() => {
      axios.get('/api/notes')
      .then(res => setNotes(res.data))
    }, [])

  const createNote = e =>{
    e.preventDefault()
    axios.post('/api/notes', { title:newNote.title, text:newNote.text })
    .then(res => setNotes(p => [res.data, ...p]))
    setNewNote(p => ({title:'', text:''}))

  }

  const editNote = (title, text) =>{
    setEditingNote(p =>( {...p, title:title, text:text, state:true}))
  }
  const confirmEdit = id =>{
    axios.put(`/api/notes/${id}`, { title: editingNote.title, text: editingNote.text })
    setNotes(notes.map(n =>(n._id == id ? editingNote : n)))
    setEditingNote(p => ({...p, state:false}))
  }
  const deleteNote = id =>{
    const newList = []
    notes.forEach(n => n._id !== id ? newList.push(n) : null)
    axios.delete(`/api/notes/${id}`)
    .then(res => setNotes(newList))
  }

  return (
    <div className="App">
      <header>Lista de Notas</header>

      <form onSubmit={createNote}>

        <input onChange={e => setNewNote(p => ({...p, title:e.target.value}))} value={newNote.title} type='text' placeholder='Titulo'></input>
        <input onChange={e => setNewNote(p => ({...p, text:e.target.value}))} value={newNote.text} type='text' placeholder='Texto'></input>
        <button type='submit'>Crear Nota</button>

      </form>

      <div>
      {notes.map(n =>(
        <div style={{display:'flex', alignItems:'center', flexDirection:'column', backgroundColor:'grey', margin:'10px 100px', padding:'10px'}}>
            <b
            style={{display:editingNote.state ? 'none' : ''}}>{n.title}</b>
            <p
            style={{display:editingNote.state ? 'none' : ''}}>
            {n.text}
            </p>
            <input 
            style={{display:editingNote.state ? '' : 'none'}}
            onChange={e => setEditingNote(p => ({...p, title:e.target.value}))} 
            value={editingNote.title} type='text' placeholder='Titulo'></input>
            <input 
            
            style={{display:editingNote.state ? '' : 'none'}}
            onChange={e => setEditingNote(p => ({...p, text:e.target.value}))} 
            value={editingNote.text} type='text' placeholder='Texto'></input>
       
            <div >
              <button onClick={_ => deleteNote(n._id)}>Eliminar Nota</button>
              <button style={{display:editingNote.state ? 'none' : ''}} onClick={_ => editNote(n.title, n.text)}>Editar Nota</button>
              <button onClick={_ => confirmEdit(n._id)} style={{display:editingNote.state ? '' : 'none'}} >Confirmar</button>
              <button onClick={_ => setEditingNote(p => ({...p, state:false}))} style={{display:editingNote.state ? '' : 'none'}} >Cancelar</button>
            </div>
            
        </div>
      ))}
      </div>
    </div>
  );
}

export default App;
